export const BEFORE_ENTER = 'beforeEnter'
export const ENTER = 'enter'
export const AFTER_ENTER = 'afterEnter'
export const ENTER_CANCELLED = 'enterCancelled'

export const BEFORE_LEAVE = 'beforeLeave'
