<template>
  <div class="vi-progress-circle">
    <svg
      :width="radius"
      :height="radius"
      viewBox="0 0 100 100"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg">
      <circle class="vi-progress-background"
        r="50"
        cx="50"
        cy="50"
        fill="transparent"/>
      <circle class="vi-progress-bar"
        r="50"
        cx="50"
        cy="50"
        fill="transparent"
        :stroke-dasharray="dashArray"
        :stroke-dashoffset="dashOffset"/>
    </svg>
    <slot></slot>
  </div>
</template>

<script>

const COMPONENT_NAME = 'base-progress-circle'

export default {
  name: COMPONENT_NAME,
  props: {
    radius: {
      type: Number,
      default: 100
    },
    percent: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      // 周长
      dashArray: Math.PI * 100
    }
  },
  computed: {
    dashOffset() {
      return (1 - this.percent) * this.dashArray
    }
  }
}
</script>

<style lang="stylus">
@import "~@/common/stylus/var/color.styl"

.vi-progress-circle
  position: relative
  circle
    stroke-width: 8px
    transform-origin: center
    &.vi-progress-background
      transform: scale(0.9)
      stroke: $color-theme-d
    &.vi-progress-bar
      transform: scale(0.9) rotate(-90deg)
      stroke: $color-theme
</style>



